import Image from "next/image"
import twitter from "../../public/projeto_twitter.png"
import platform from "../../public/projeto_users.png"
import weather from "../../public/projeto_weather"

const Projects = () => {
  return (
    <div className="grid grid-cols-3">
        <div>
          <Image src={twitter} className="h-80 w-screen object-cover"/>
        </div>
        <div>
          <Image src={platform} className="h-80 w-screen object-cover"/>
        </div>
    </div>
  )
}

export default Projects